.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 32px 0;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 100;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  @media (width < 576px) {
    padding: 80px 15px;
  }
}

.content {
  display: flex;
  flex-flow: column nowrap;
  box-sizing: border-box;
  width: auto;
  max-width: 90%;
  max-height: 90%;
  border-radius: 24px;
  background-color: #ffffff;
  outline: 0 none;
  //overflow-x: hidden;
  overflow-y: auto;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
  min-width: 100px;
  min-height: 100px;
  padding: 20px;

  & > .block {
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin: 0 auto;
    //flex-grow: 1;
    height: 200px;
    padding: 8px;

    & > .title {
      font-family: sans-serif;
      font-size: 14px;
      color: orangered;
    }

    & > img {
      height: 150px;
      width: auto;
    }
  }
}
